label .menu {
  position: fixed;
  right: -100px;
  top: -100px;
  z-index: 20;
  width: 200px;
  height: 200px;
  background: #00C6B800;
  border-radius: 20% 20% 20% 20%;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #FFF;
  cursor: none;
  animation: name 0.5s 1;
  border-radius: 30px;
}

label .hamburger {
  position: absolute;
  top: 135px;
  left: 50px;
  width: 30px;
  height: 5px;
  /* background: #0c1821; */
  background: #00C6B8;
  display: block;
  -webkit-transform-origin: center;
  transform-origin: center;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}
label .hamburger:after, label .hamburger:before {
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  /* background: #0c1821; */
  background: #00C6B8;

  
}

label .hamburger:before { top: -10px; }

label .hamburger:after { bottom: -10px; }

label input { display: none; }

label input:checked + .menu {
  box-shadow: 0 0 0 100vw #00C6B8, 0 0 0 400px #00C6B8; overflow: hidden;border-radius: 0%;
  
}

label input:checked + .menu .hamburger {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  
}

label input:checked + .menu .hamburger:after{
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  bottom: 0;

}

label input:checked + .menu .hamburger:before {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  top: 0;
  
}

label input:checked + .menu  + ul{ opacity: 1; animation: slidein 1s 1; pointer-events: all;}  

label ul {
  cursor: none;
  pointer-events: none;
  z-index: 200;
  position: fixed;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  -webkit-transition: .5s 0s ease-in-out;
  transition: .5s 0s ease-in-out;
  list-style: none;
  height: 100vh;
}
@keyframes slidein {
    0%{left: 45%;opacity: 0;}100%{left: 50%;opacity: 1;}
}

